@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.eot'); /* IE9 Compat Modes */
  src: local('Roboto'), local('Roboto-Regular'),
    url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('../assets/fonts/roboto-v19-latin_cyrillic-ext-regular.svg#Roboto') format('svg'); /* Legacy iOS */
}

html,
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.sova-ck-main {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.sova-ck-chat {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: 100%;
  width: 100%;
}

.sova-scrollbar {
  padding: 1rem 0 0 0;
}
